.heading {
    font-size: 20px;
    color:#373936;
    text-align:center;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    margin-bottom: 10px;
    
}

.job-application-container {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 20px;
    border: 2px solid #cbd5e0;
    border-radius: 50px;
    margin: 20px;
    width: 40%;
    box-shadow: 3px 2px 10px rgb(255, 0, 0);
    transform: translateZ(0);
    transition: transform 0.3s ease;
    max-width: 1000px;
    border: 1px solid #cbd5e0;
    border-radius: 50px;
    transform: translateZ(0);
    transition: transform 0.3s ease;
    align-items: center;
    justify-content: center;
    height: auto;
    background-color: transparent;
    margin: 0 auto;
    padding: 10px;

    @media (max-width: 768px) {
        flex-direction: column;
    }

    @media (max-width: 480px) {
        padding: 5px;
    }

    @media (max-width: 320px) {
        padding: 3px;
    }

    @media (max-width: 240px) {
        padding: 2px;
    }

    @media (max-width: 200px) {
        padding: 1px;
    }

    @media (max-width: 160px) {
        padding: 0px;
    }

    @media (max-width: 1200px) {
        padding: 0px;
    }

    @media (max-width: 1000px) {
        padding: 0px;
    }

    @media (max-width: 800px) {
        padding: 0px;
    }

    @media (max-width: 600px) {
        padding: 0px;
    }

    @media (max-width: 400px) {
        padding: 0px;
    }

    @media (max-width: 300px) {
        padding: 0px;
    }

    @media (max-width: 768px) {
        width: 100%;
        padding: 10px;
    }

    &:focus-within {
        transform: translateZ(10px);
    }

    &:hover {
        transform: translateZ(10px);
    }

    form {
        .block {
            margin-bottom: 10px;

            @media (max-width: 768px) {
                margin-bottom: 5px;
            }

            @media (max-width: 480px) {
                margin-bottom: 2px;
            }

            @media (max-width: 320px) {
                margin-bottom: 1px;
            }

            @media (max-width: 240px) {
                margin-bottom: 0px;
            }
        }

        input[type="text"],
        input[type="email"],
        input[type="tel"],
        input[type="file"],
        input[type="number"],
        textarea {
            width: 100%;
            padding: 8px;
            margin-top: 4px;
            border: 1px solid #373936;
            border-radius: 4px;
            background-color: #fff;
            outline: none;
            transition: border-color 0.2s;

            &:focus {
                border-color: #ff0000;
            }
        }

        label {
            font-weight: 600;
            margin-bottom: 4px;
            font-family: "Poppins", sans-serif;
            display: block;
            color: #2d3748;
            font-size: 14px;
            line-height: 1.5;
            text-transform: uppercase;
            letter-spacing: 0.05em;

            .required {
                color: #ff0000;
            }
        }


        button {
            background-color: #ff0000;
            color: #fff;
            font-weight: 600;
            padding: 8px 16px;
            margin: 15px auto;
            border-radius: 6px;
            border: none;
            cursor: pointer;

            &:disabled {
                opacity: 0.5;
                cursor: not-allowed;
            }

            &:hover {
                background-color: #ff0000;
            }

            &:focus {
                outline: none;
            }
            transition: background-color 0.2s;
            margin-top: 10px;
            display: inline-block;
            text-decoration: none;
            font-size: 14px;
            line-height: 1.5;
            text-transform: uppercase;
            letter-spacing: 0.05em;
            font-family: "Poppins", sans-serif;
            font-weight: 600;
            cursor: pointer;

            @media (max-width: 768px) {
                margin-top: 5px;
            }

            @media (max-width: 480px) {
                margin-top: 2px;
            }

            @media (max-width: 320px) {
                margin-top: 1px;
            }

            @media (max-width: 240px) {
                margin-top: 0px;
            }
        }

        .error {
            color: #ff0000;
            font-size: 12px;
            margin-top: 4px;

            @media (max-width: 768px) {
                margin-top: 5px;
            }

            @media (max-width: 480px) {
                margin-top: 2px;
            }
        }
    }
}
